/* width */
::-webkit-scrollbar {
  width: 10px; }

/* Track */
::-webkit-scrollbar-track {
  box-shadow: inset 1px 1px 5px #838383; }

/* Handle */
::-webkit-scrollbar-thumb {
  background: #40d5bb;
  background: linear-gradient(to bottom, #40d5bb 0%, #1694d7 55%, #00aeff 100%);
  box-shadow: 0px 9px 12px 0px rgba(131, 131, 131, 0.4);
  border-radius: 10px; }

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  box-shadow: 2px 3px 25px rgba(131, 131, 131, 0); }

body div ::-webkit-scrollbar {
  width: 6px; }

